import React, { Component } from 'react';
import './WorkoutContainer.css';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import WorkoutCard from '../components/WorkoutCard';
import Chart from '../components/Chart';

class WorkoutContainer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      totalTime: 0,
      numCardio: 0,
      numLegs: 0,
      numChest: 0,
      numBack: 0,
      numCore: 0,
      exercises: this.props.exercises,
    }
  }

  changeUnit = (val, index) => {
    let ex = [...this.state.exercises];
    ex[index].unit = val; 
    this.setState({
      exercises: ex,
    })
  }
  changeNum = (val, index) => {
    let ex = [...this.state.exercises];
    ex[index].num = val; 
    this.setState({
      exercises: ex,
    })
  }

  createSummary = () => {
    let totalTime = 0;
    let numCardio = 0;
    let numBack = 0;
    let numLegs = 0;
    let numChest = 0;
    let numCore = 0;

    for (let i = 0; i < this.props.exercises.length; i++) {
      totalTime += this.props.exercises[i].time;
      switch(this.props.exercises[i].category) {
        case 'Chest':
          numChest++;
          break;
        case 'Legs':
          numLegs++;
          break;
        case 'Back':
          numBack++;
          break;
        case 'Cardio':
          numCardio++;
          break;
        case 'Core':
          numCore++;
          break;
        default:
          break;
      } 
    }
    this.setState({
      totalTime: totalTime,
      numCardio: numCardio,
      numBack: numBack,
      numLegs: numLegs,
      numChest: numChest,
      numCore: numCore,
    });
  }

  onDragEnd = (result) => {
    let exercises = [...this.state.exercises];
    const from = result.source.index;
    let to = '';

    if (result.destination !== null && result.destination.droppableId !== 'trash' ) {
      to = result.destination.index;
      exercises[to] = exercises[from];
      if (to < from) {
        // moved up
        for (let i = to+1; i <= from; i++) {
          exercises[i] = this.state.exercises[i-1];
        }
      } else {
        // moved down
        for (let i = to-1; i >= from; i--) {
          exercises[i] = this.state.exercises[i+1];
        }
      }   
      this.setState({ exercises: exercises });
    } else {
      exercises.splice(from, 1);
      this.setState({ exercises: exercises });
      console.log("exercises1: ", exercises);
      this.props.updateExerciseList(exercises);
    }
  }

  getStyle = (style, snapshot) => {
    if (!snapshot.isDropAnimating) {
      return style;
    } else if (snapshot.isDropAnimating && (snapshot.draggingOver === null || snapshot.isdragginOver === 'trash')) {
      return {
        ...style,
        // cannot be 0, but make it super tiny,
        opacity: '0.2',
        transitionDuration: `0.001s`,
      };
    } else {
      return style;
    }
  };

  componentWillReceiveProps = (nextProps) => {
    this.createSummary();
    if (nextProps.exercises.length > this.state.exercises.length) {
      let exercises = [...this.state.exercises];
      exercises.push(nextProps.exercises[nextProps.exercises.length-1]);
      this.setState({ exercises: exercises });
    }
  }

  render() {
    let trashCanCode = '';
    if (this.props.exercises.length > 0) {
      trashCanCode = 
        <Droppable droppableId="trash">
          {(provided, snapshot) => (
            <div 
              ref={provided.innerRef}
              {...provided.draggableProps}
              >
                <button className="trash-btn shadow-1"
                  style={{ backgroundColor: snapshot.isDraggingOver ? '#FFF' : '#777' }} >
                  <div className="trash-text"
                    style={{ color: snapshot.isDraggingOver ? '#000' : '#FFF' }}>  
                    Drag here to delete
                  </div>
                </button>
            </div>
          )}
        </Droppable>;   
    }


    return (
      <div className="workout-container">
        <div className="workout-inner-container">
          <DragDropContext onDragEnd={this.onDragEnd} >
            <Droppable droppableId="list">
              {provided => (
                <div
                  ref={provided.innerRef}
                  {...provided.draggableProps}
                  >
                  {this.state.exercises.map((ex, i) => (
                    <Draggable draggableId={'draggable-' + i} index={i}>
                        {(provided, snapshot) => (
                          <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            style={this.getStyle(provided.draggableProps.style, snapshot)}
                            >
                            <WorkoutCard
                              key={i}
                              index={i}
                              exercises={ex}
                              changeUnit={this.changeUnit}
                              changeNum={this.changeNum}
                              clicked={this.clicked}
                              isDragging={snapshot.isDragging}
                              draggingOver={snapshot.draggingOver}
                              />
                          </div>
                        )}
                      </Draggable>
                  ))}
                </div>
              )}
            </Droppable>
            {trashCanCode}
          </DragDropContext>
        </div>
        <div className="workout-summary-container shadow-1">
          {/*Total Time: {this.state.totalTime} minutes*/}
          <Chart data={this.state} />
        </div>
      </div>
    );
  }
}

export default WorkoutContainer;